/* src/App.css */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  text-align: center;
}

.nav-list {
  list-style-type: none;
  padding: 0;
  display: flex;
  justify-content: center;
}

.nav-list li {
  margin: 0 15px;
}

.nav-list li a {
  color: #61dafb;
  text-decoration: none;
}

main {
  padding: 20px;
}

section {
  margin: 40px 0;
}

footer {
  text-align: center;
  padding: 20px;
  background-color: #282c34;
  color: white;
  position: fixed;
  width: 100%;
  bottom: 0;
}
